@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.intro{margin:5vh;height:clamp(400px, 70vh, 1000px);width:clamp(250px, 70vw, 2000px);border:2px solid #fff;display:flex;flex-direction:column;align-items:center;background-color:rgba(0,0,0,.1);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);color:#fff;font-family:"Josefin Sans",sans-serif}.intro img{margin:3vh 0;width:70%;max-width:300px}.intro .intro__info{height:100%;display:flex;flex-direction:column;align-items:center}.intro .intro__info .name{font-size:3rem;color:#ff7272}@media only screen and (max-width: 1000px){.intro .intro__info .name{font-size:2rem}}.intro .intro__info .aka{border:1px s;margin:10px 0px;display:flex;align-items:flex-start;font-size:1.2rem}@media only screen and (max-width: 1000px){.intro .intro__info .aka{font-size:1rem}}.intro .intro__info .nickname{font-size:2.5rem;color:#e4e276}@media only screen and (max-width: 1000px){.intro .intro__info .nickname{font-size:1.7rem}}.intro .intro__info .info{margin:20px;display:grid;place-items:center}
.navspace{color:#fff;border:1px solid #fff;padding:1.2vh 6vw}
.mainbg{height:100vh;width:100vw;background-image:url(/react-portfolio/static/media/background.d7da8c0e.jpg);background-position:center;background-repeat:no-repeat;display:flex;flex-direction:column;align-items:center}
